@import '../../../styles/utils/vars';
@import '../../../styles/utils/mixins';
@import '../../../styles/utils/container';
@import '../../../styles/heading-styles';

.product-details {
  padding-block: calc(24px + $nav-height-mobile) 56px;

  @include on-tablet {
    padding-bottom: 64px;
  }

  @include on-desktop {
    padding-block: calc(24px + $nav-height-desktop) 81px;
  }

  &__product {
    margin-block: 32px 56px;

    @include on-tablet {
      margin-block: 40px 64px;
    }

    @include on-desktop {
      margin-bottom: 80px;
    }
  }

  &__info {
    &--tech {
      margin-top: 56px;

      @include on-tablet {
        margin-top: 64px;
      }

      @include on-desktop {
        margin: 0;
      }
    }

    @include on-desktop {
      @include page-grid;

      &--about {
        grid-column: 1 / 13;
      }

      &--tech {
        grid-column: 14 / -1;
      }
    }
  }

  &__slider {
    margin-block: 56px;

    @include on-tablet {
      margin-block: 64px;
    }

    @include on-desktop {
      margin-block: 80px;
    }
  }
}

.about {
  &__title {
    padding-bottom: 16px;
    box-sizing: border-box;
    border-bottom: 1px solid $shadow-color;
  }

  &__text-Block {
    margin-top: 32px;

    &--text {
      margin-top: 16px;
      color: $nav-link-color;
    }
  }
}

.tech {
  &__title {
    padding-bottom: 16px;
    box-sizing: border-box;
    border-bottom: 1px solid $shadow-color;
    margin-bottom: 22px;

    @include on-tablet {
      margin-bottom: 15px;
    }
  }

  &__textBlock {
    display: flex;
    justify-content: space-between;
    margin-top: 8px;

    &--left {
      color: $nav-link-color;
    }
  }
}
