.icon {
  background-repeat: no-repeat;
  background-position: center;

  &__logo {
    background-image: url(../../images/Header/logo.svg);
    background-size: contain;
    width: 64px;
    height: 22px;
    margin: 13px 16px;

    @include on-desktop {
      width: 80px;
      height: 28px;
      margin: 18px 24px;
    }
  }

  &__menu {
    padding: 16px;
    width: 16px;
    height: 16px;
    background-image: url(../../images/Header/manu.svg);
    box-shadow: -1px 0 0 0 $shadow-color;
  }

  @include on-tablet {
    &__favourite,
    &__card {
      padding: 16px;
      width: 16px;
      height: 16px;
      box-shadow: -1px 0 0 0 $shadow-color;

      @include on-desktop {
        padding: 24px;
      }
    }

    &__favourite {
      background-image: url(../../images/Header/favourite.svg);
    }

    &__card {
      background-image: url(../../images/Header/card.svg);
    }

    &__menu {
      display: none;
    }
  }
}
