@import '../../styles/utils/vars';
@import '../../styles/utils/mixins';
@import '../../styles/utils/link';
@import '../../styles/utils/icon';
@import '../../styles/utils/container';

.footer {
  display: flex;
  box-shadow: 0 -1px 0 0 $shadow-color;

  &__logo {
    width: 89px;
    height: 32px;
    margin: 0;
  }

  &__link {
    font-family: Mont-SemiBold, sans-serif;
    text-transform: initial;
    padding: 0;
    cursor: pointer;
  }

  &__centerBlock {
    display: flex;
    flex-direction: column;
    gap: 16px;

    @include on-tablet {
      flex-direction: row;
      gap: 13.5px;
    }

    @include on-desktop {
      gap: 107px;
    }

    &--link {
      width: min-content;
      padding: 0;
    }
  }

  &__rightBlock {
    display: flex;
    gap: 16px;
    justify-content: center;
    align-items: center;

    &--arrow-link {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 32px;
      height: 32px;
      border: 1px solid $footer-arrow-border;
      border-radius: 50%;
      cursor: pointer;
    }

    &--arrow-img {
      width: 16px;
      height: 16px;
      background-image: url(../../images/Footer/arrow.svg);
    }
  }

  &__container {
    display: flex;
    flex-direction: column;
    padding-block: 32px;
    gap: 32px;

    @include on-tablet {
      justify-content: space-between;
      flex-direction: row;
      padding-inline: 32px;
    }
  }
}
