@import '../../../../styles/utils/vars';
@import '../../../../styles/utils/mixins';

.categories {
  &__items-counter {
    font-family: Mont-Regular, sans-serif;
    font-size: 14px;
    font-weight: 600;
    color: $nav-link-color;
    margin-top: 4px;
  }

  &__block {
    @include page-grid;
    margin-top: 24px;
    row-gap: 32px;

    &--photo {
      display: block;
      width: 100%;
      border-radius: 10px;
      margin-bottom: 24px;
    }

    &--title {
      text-decoration: none;
    }

    &--phones,
    &--tablets,
    &--accessories {
      grid-column: 1 / -1;
    }

    @include on-tablet {
      &--phones {
        grid-column: 1 / 5;
      }

      &--tablets {
        grid-column: 5 / 9;
      }

      &--accessories {
        grid-column: 9 / 13;
      }
    }

    @include on-desktop {
      &--phones {
        grid-column: 1 / 9;
      }

      &--tablets {
        grid-column: 9 / 17;
      }

      &--accessories {
        grid-column: 17 / 25;
      }
    }
  }
}
