@import '../../../styles/utils/vars';
@import '../../../styles/utils/mixins';
@import '../../../styles/heading-styles';

.productCard {
  &__item {
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: 220px;
    background-color: $bg-button-color;
    padding: 32px 31px;
    border: 1px solid $shadow-color;
    border-radius: 8px;
    transition-duration: $transition-duration;
    text-decoration: none;
    color: $text-color;

    @include on-tablet {
      width: auto;
    }
  }

  &__item-top {
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    justify-content: space-between;
    padding-block: 24px 8px;
    height: 100px;

    &--priceBlock {
      display: flex;
      gap: 8px;
    }

    &--priceBlock-fullPrice {
      font-family: Mont-Regular;
      color: $nav-link-color;
      font-size: 22px;
      text-decoration: line-through;
    }
  }

  &__item-center {
    display: flex;
    flex-direction: column;
    gap: 8px;
    border-top: 1px solid $shadow-color;
    padding-block: 16px;
  }

  &__item-center-param {
    display: flex;
    justify-content: space-between;

    &--left {
      color: $nav-link-color;
    }

    &--right {
      color: $text-color;
      font-family: Mont-SemiBold, sans-serif;
      font-size: 12px;
    }
  }

  &__item-bottom {
    display: flex;
    justify-content: space-between;

    &--cart {
      width: 100px;
      height: 40px;
      border-radius: 8px;
      background-color: $button-color;
      border: none;
      font-family: Mont-Regular, sans-serif;
      font-size: 14px;
      color: $bg-button-color;
      cursor: pointer;

      @include on-tablet {
        width: 125px;
      }

      @include on-desktop {
        width: 160px;
      }
    }

    &--favourite {
      min-width: 40px;
      min-height: 40px;
    }
  }

  &__imageBlock {
    display: grid;
    place-items: center;

    &--image {
      display: block;
      object-fit: contain;
      width: 148px;
      height: 139px;

      @include on-tablet {
        width: 173px;
        height: 181px;
      }

      @include on-desktop {
        width: 208px;
        height: 196px;
      }
    }
  }
}
