@import '../../styles/utils/vars';
@import '../../styles/utils/mixins';
@import '../../styles/utils/link';
@import '../../styles/utils/icon';


.nav {
  position: fixed;
  z-index: 3;
  width: 100%;
  height: $nav-height-mobile;
  display: flex;
  align-items: center;
  box-sizing: border-box;
  box-shadow: 0 1px 0 0 $shadow-color;
  background-color: $bg-button-color;

  @include on-desktop {
    height: $nav-height-desktop;
  }

  &__leftBlock {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include on-tablet {
      width: min-content;
    }
  }

  &__rightBlock {
    display: flex;
    justify-content: space-between;

    @include on-tablet {
      width: 100%;
    }

    &--left {
      display: none;

      @include on-tablet {
        margin-left: 16px;
        display: flex;
        gap: 32px;
        height: 48px;
      }

      @include on-desktop {
        margin-left: 24px;
        height: 64px;
        gap: 64px;
      }
    }

    &--right {
      display: flex;
      align-items: center;
    }
  }
}
