@import '../../styles/utils/vars';
@import '../../styles/utils/mixins';
@import '../../styles/buttonStyle';

.slider {
  &__hBLock {
    display: flex;
    align-items: center;
    justify-content: space-between;

    &--buttons {
      display: flex;
      gap: 16px;
    }

    &--buttons-imgLeft {
      rotate: 180deg;
    }

    &--title {
      @media (max-width: 340px) {
        width: 130px;
      }
    }
  }

  &__list {
    display: flex;
    box-sizing: border-box;
    gap: 16px;
    padding: 0;
    margin: 0;
    list-style-type: none;
    overflow: hidden;
    margin: 24px 0 0 16px;

    @include on-tablet {
      margin-left: 24px;
    }

    @include on-desktop {
      max-width: 1136px;
      margin-inline: auto;
    }
  }
}
