@import '../../../../styles/utils/vars';
@import '../../../../styles/utils/mixins';

.welcome {
  &__banner {
    box-sizing: border-box;
    margin-top: 24px;

    &--list {
      display: flex;
      list-style-type: none;
      overflow: hidden;
      padding: 0;
      margin: 0;
    }

    &--item {
      display: grid;
      place-items: center;
      min-width: 100%;
      transition-duration: $transition-duration;
      transition-timing-function: $transition-timing-function;
    }

    &--image {
      display: block;
      object-fit: cover;
      width: 100%;
    }

    &--button-left,
    &--button-right {
      display: none;
    }

    @include on-tablet {
      display: flex;
      gap: 19px;
      margin-top: 32px;
      padding-inline: 24px;

      &--image {
        width: $tablet-banner-img-width;
        height: $tablet-banner-height;
      }

      &--button-left,
      &--button-right {
        display: block;
        height: $tablet-banner-height;
        border-radius: 48px;
        min-width: 32px;
      }

      &--leftImg {
        rotate: 180deg;
      }
    }

    @include on-desktop {
      padding-inline: 32px;
      max-width: $desktop-min-width;
      margin-inline: auto;
      margin-top: 56px;
      gap: 16px;

      &--image {
        width: 100%;
      }

      &--button-left,
      &--button-right,
      &--image {
        height: $desktop-banner-height;
      }
    }
  }

  &__bunner-bottom {
    display: flex;
    justify-content: center;
    gap: 9px;
    margin-top: 18px;

    &--button {
      width: 14px;
      height: 4px;
      background-color: $shadow-color;
      border: none;
      cursor: pointer;
    }

    &--button-active {
      background-color: $text-color;
    }
  }
}
