@import '../styles/utils/vars';
@import '../styles/utils/mixins';

h1,
h2,
h3,
h4 {
  color: $text-color;
  line-height: 100%;
}

h1 {
  font-family: Mont-Bold, sans-serif;
  font-size: 32px;
  letter-spacing: -0.01em;

  @include on-tablet {
    font-family: Mont-Bold, sans-serif;
    font-size: 48px;
  }
}

h2 {
  font-family: Mont-Bold, sans-serif;
  font-size: 22px;

  @include on-tablet {
    font-size: 32px;
  }
}

h3 {
  font-family: Mont-SemiBold, sans-serif;
  font-size: 20px;

  @include on-tablet {
    font-family: Mont-Bold, sans-serif;
    font-size: 22px;
  }
}

h4 {
  font-family: Mont-SemiBold, sans-serif;
  font-size: 16px;
  color: #0f0f11;

  @include on-tablet {
    font-size: 20px;
  }
}

.uppercase {
  font-family: Mont-Bold, sans-serif;
  font-size: 12px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
}

.buttons {
  font-family: Mont-SemiBold, sans-serif;
  font-size: 14px;
}

.body-text {
  font-family: Mont-Regular, sans-serif;
  font-size: 14px;
}

.small-text {
  font-family: Mont-Regular, sans-serif;
  font-size: 12px;
}
