@import '../../../styles/utils/vars';
@import '../../../styles/utils/mixins';

.navigate-path {
  display: flex;
  align-items: center;
  margin-bottom: 24px;
  gap: 8px;

  &__link {
    display: flex;
    font-family: Mont-Regular, sans-serif;
    font-size: 12px;
    color: $nav-link-color;
    height: 16px;
    text-decoration: none;

    &--block {
      cursor: not-allowed;
      pointer-events: none;
    }
  }

  @include on-tablet {
    margin-bottom: 40px;
  }
}
